*{
    margin: 0;
    /* padding: 0; */
    box-sizing: border-box; 
    color: rgb(46, 80, 104);
}

#education{
    margin-top: 1.5em;
    margin-bottom: 1.5em;
}

#personal{
    margin-top: 1.5em;
    margin-bottom: 1.5em;
}

body{
    min-height: 100vh;
    background: #0c192c;
}

.container{
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.bubbles{
    z-index: -100;
    position: relative;
    display: flex;
}

.bubbles span{
    position: relative;
    width: 30px;
    height: 30px;
    background: #4fc3dc;
    margin: 0 4px;
    border-radius: 50%;
    box-shadow: 0 0 0 10px #4fc3dc44,
    0 0 50px #4fc3dc,
    0 0 100px #4fc3dc;
    animation: animate 15s linear infinite;
    animation-duration: calc(125s / var(--i));
}

.bubbles span:nth-child(even){
    background: #ff2d7d;
    box-shadow: 0 0 0 10px #ff2d7d44,
    0 0 50px #ff2d7d,
    0 0 100px #ff2d7d;
}

@keyframes animate{
    0%{
        transform: translateY(100vh) scale(0);
    }
    100%{
        transform: translateY(-10vh) scale(1);
    }
}